<template>
  <div class="detail">
    <van-nav-bar
      ref="toolbar"
      style="background:rgba(255, 255, 255, 0)"
      left-arrow
      fixed
      @click-left="back"
      @click-right="goBookrack"
    >
      <template #right>
        <van-icon name="bar-chart-o" size="18" />
      </template>
    </van-nav-bar>
    <div class="book-info">
      <div class="info-box clearfix">
        <div class="book-text fl">
          <div class="book-title">{{bookDetail.title}}</div>
          <div class="book-author">作者：{{bookDetail.author}}</div>
          <div
            class="book-type"
          >{{bookDetail.minorCateV2}} · {{bookDetail.allowMonthly?"完结":"连载"}} · {{bookDetail.wordCount}}万字</div>
        </div>
        <div class="book-img fr">
          <img class="auto-img" :src="bookDetail.cover" alt />
        </div>
      </div>
    </div>

    <div class="book-hot">
      <div class="book-score">
        <span>{{bookDetail.rating.score}}</span>
        <span>分</span>
        <p>{{bookDetail.rating.tip}}</p>
      </div>
      <div class="book-retentionRatio">
        <p>
          <span>{{bookDetail.totalFollower}}</span>万人气 ·
          <span>{{bookDetail.latelyFollower}}</span>万在读
        </p>
        <p>
          <span>{{bookDetail.retentionRatio}}</span>%读者留存
        </p>
      </div>
    </div>

    <van-tabs
      v-model="active"
      animated
      swipeable
      line-width="15px"
      color="#f99222"
      @click="showActive"
    >
      <van-tab title="详情" class="detail-box">
        <div class="book-shortIntro" :class="{'close':isClose}" @click="toggleIsClose">
          <div class="shortIntro-text">{{bookDetail.longIntro}}</div>
          <div class="arrow">
            <img class="auto-img" src="../assets/img/arrow.png" alt />
          </div>
        </div>
        <div class="book-directory" @click="goDirectory">
          查看目录
          <div
            class="book-lastChapter"
          >{{bookDetail.allowMonthly?"已完结 共":"连载中 更新至"}}{{bookDetail.chaptersCount}}章</div>
          <div class="book-updated">
            {{bookDetail.updated | formatDate('yyyy-MM-dd')}}更新
            <van-icon name="arrow" />
          </div>
        </div>
      </van-tab>
      <van-tab title="评论" class="discuss-box">
        <div class="discuss-list">
          <div class="discuss" v-for="(item,index) in discussionsData" :key="index">
            <div class="discuss-author">
              {{item.author.nickname}}
            </div>
            <div class="discuss-content">
              {{item.title}}
            </div>
            <div class="discuss-time">
              {{item.updated | formatDate('yyyy-MM-dd hh:mm:ss')}}
            </div>
          </div>
        </div>
      </van-tab>
    </van-tabs>

    <div class="recommend-box">
      <div class="about-box">
        <h2>相关推荐</h2>
        <span @click="changebooksData">换一批</span>
      </div>
      <div class="recommend-books">
        <div
          class="book-box"
          v-for="(item,index) in booksData"
          :key="index"
          @click="goDetail(item._id)"
        >
          <img class="auto-img" :src="item.cover" alt />
          <div class="book-title">{{item.title}}</div>
        </div>
      </div>
    </div>

    <div class="btns-box">
      <div class="btn btn-addBookrack" v-if="bookState.isAtClassify">已加入书架</div>
      <div class="btn btn-addBookrack" @click="addClassify" v-else>加入书架</div>
      <div class="btn btn-read" @click="goChapters">{{bookState.isReaded?"继续阅读":"开始阅读"}}</div>
    </div>
  </div>
</template>

<script>
import "../assets/less/detail.less";
export default {
  data() {
    return {
      offsetTop: 0,
      active: 0,
      bgc: "rgba(255, 255, 255, 0)",
      isSpread: false,
      recommendData: [],
      booksData: [],
      isClose: true,
      page:1,
      // 评论
      discussionsData:[]
    };
  },
  computed: {
    // 书籍详情
    bookDetail() {
      return this.$store.state.detailStore.bookDetail;
    },

    // 书籍状态
    bookState() {
      // 书籍状态
      let state = {
        bookOrder: 0,
        isAtClassify: false,
        isReaded: false,
        historyTitle: "无",
        time: "",
      };

      if (this.$store.state.bookrackStore.historyData.length == 0) return state;

      this.$store.state.bookrackStore.historyData.map((v) => {
        if (this.bookDetail._id == v._id) {
          // console.log("v.bookState==>", v.bookState);
          state = v.bookState;
        }
      });
      return state;
    },
  },
  created() {
    // console.log(this.bookState);

    this.getRecommendData();

    this.getDirectoryList();

    this.getDiscussions();
  },
  mounted() {
    let toolbar = this.$refs.toolbar.$el;
    window.addEventListener(
      "scroll",
      () => {
        this.offsetTop =
          window.pageYOffset ||
          document.documentElement.scrollTop ||
          document.body.scrollTop;
        let opt = 1;
        opt = Math.round((this.offsetTop / 167) * 10) / 10;
        toolbar.style.background = "rgba(255, 255, 255, " + opt + ")";
      },
      true
    );
  },
  methods: {
    back() {
      this.$router.go(-1);
    },

    // 前往书架
    goBookrack() {
      this.$router.push({ name: "Bookrack" });
    },

    // 前往目录
    goDirectory() {
      this.$router.push({ name: "Directory" });
    },

    // 书籍目录
    getDirectoryList() {
      this.isClose = true;
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 1000,
        loadingType: "spinner",
      });
      this.axios({
        method: "GET",
        url: "/book-chapters/" + this.$store.state.detailStore.bookOriginId,
      })
        .then((result) => {
          this.$toast.clear();
          this.$store.dispatch(
            "detailStore/setDirectoryListByLink",
            result.data.chapters
          );
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },

    // 获取书籍评论
    getDiscussions(){
      this.axios({
        method:"GET",
        url:`/book/discussions?&start=${this.page}&limit=10&book=${this.bookDetail._id}`
      }).then(result=>{
        this.discussionsData=result.data.posts
        // console.log("书籍评论=>",this.discussionsData)
      })
    },

    // 获取推荐书籍
    getRecommendData() {
      this.axios({
        method: "GET",
        url: "/recommend/" + this.$store.state.detailStore.bookId,
      }).then((result) => {
        this.recommendData = result.data.books;
        this.recommendData.map((v) => {
          v.cover = unescape(v.cover.slice(7));
        });
        // console.log("this.recommendData==>", this.recommendData);
        this.changebooksData();
      });
    },

    // 点击书籍，根据书籍id获取书籍详情，前往书籍详情页面
    goDetail(id) {
      // 滚动条回到最顶部
      window.scrollTo(0, 0);
      this.$store.dispatch("detailStore/setBookIdListById", id);
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.axios
        .all([
          this.axios({
            method: "GET",
            url: "/book-info/" + id,
          }),
          this.axios({
            method: "GET",
            url: "/book-sources?view=summary&book=" + id,
          }),
        ])
        .then((result) => {
          this.$toast.clear();
          this.$store.dispatch("detailStore/setBookDetail", result[0].data);
          this.$store.dispatch(
            "detailStore/setBookOriginIdListById",
            result[1].data[0]._id
          );
          this.getDirectoryList();
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },

    // 相关推荐换一批
    changebooksData() {
      let num = Math.floor(Math.random() * (this.recommendData.length - 2));
      this.booksData = this.recommendData.slice(num, num + 3);
      // console.log(num, this.recommendData.length, this.booksData);
    },

    showActive(name, title) {
      // console.log(name, title);
    },

    // 详情展开
    toggleIsClose() {
      this.isClose = !this.isClose;
    },

    setBookState() {
      if (this.$store.state.bookrackStore.historyData.length == 0) return;
      this.$store.state.bookrackStore.historyData.map((v) => {
        if (this.bookDetail._id == v._id) {
          this.bookState = v.state;
          return;
        }
      });
    },

    // 前往阅读页面
    goChapters() {
      // console.log("bookState.bookOrder==>", this.bookState.bookOrder);
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      // 获取目标章节
      let chapterDir = this.$store.state.detailStore.directoryList[
        this.bookState.bookOrder
      ];
      // 获取章节目录link
      let link = chapterDir.link;
      // 设置章节
      this.$store.dispatch("chaptersStore/setChaptersList", chapterDir);

      // console.log("link=>", link);
      this.axios({
        method: "GET",
        url: "/chapters/" + encodeURIComponent(link),
      }).then((result) => {
        this.$toast.clear();
        this.$store.dispatch(
          "chaptersStore/setChapterText",
          result.data.chapter
        );
        let state = Object.assign(this.bookState);
        // 设置书籍当前的阅读标题
        state.historyTitle = this.$store.state.detailStore.directoryList[
          state.bookOrder
        ].title;
        // 记录本书籍当前阅读时间
        state.time = new Date();
        //isReaded属性更新为true
        state.isReaded = true;
        // 更新书籍状态、更新阅读记录书籍状态
        this.$store
          .dispatch("detailStore/setBookDetailBookState", state)
          .then((res) => {
            this.$store.dispatch("bookrackStore/addHistoryData", res);
          });

        this.$router.push({ name: "Chapters" });
      });
    },

    // 加入书架
    addClassify() {
      let state = Object.assign(this.bookState);
      //isAtClassify属性更新为true
      state.isAtClassify = true;
      // 更新书籍状态、更新阅读记录书籍状态
      this.$store
        .dispatch("detailStore/setBookDetailBookState", state)
        .then((res) => {
          this.$store.dispatch("bookrackStore/addHistoryData", res);
        });
    },
  },
};
</script>